body {
  background-color: #fff;
  font-family: 'Nanum Gothic';
}

p {
  line-height: 1.6;
}

main {
  padding: 2rem;
  height: auto;
  overflow: hidden;
  margin-bottom: 50px;
}

.navBar {
  background-color: #004c98;
  padding: 1rem;
}

.nav-links {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin: 0 1rem;
}

.nav-links li a {
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  transition: 0.3s;
}

.nav-links li a:hover {
  color: skyblue;
}

.footer {
  background-color: #004c98;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
}