.contact-container {
  padding: 2rem;
  text-align: center;
  height: 100%;
}

.contact-image {
  margin: 50px 0;
  display: flex;
  justify-content: center;
}

.contact-image img {
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.contact-title {
  color: #004c98;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.contact-discription {
  color: #333;
  font-size: 1.2rem;
}